// Modern color palette with better contrast and contemporary feel
$primary: #2563eb;
$primary-dark: #1d4ed8;
$primary-light: #3b82f6;

$secondary: #64748b;
$secondary-light: #94a3b8;

$success: #10b981;
$success-light: #34d399;
$warning: #f59e0b;
$warning-light: #fbbf24;
$error: #ef4444;
$error-light: #f87171;

$dark: #0f172a;
$dark-light: #1e293b;
$white: #ffffff;
$gray-50: #f8fafc;
$gray-100: #f1f5f9;
$gray-200: #e2e8f0;
$gray-300: #cbd5e1;
$gray-400: #94a3b8;
$gray-500: #64748b;
$gray-600: #475569;
$gray-700: #334155;
$gray-800: #1e293b;
$gray-900: #0f172a;

// Light theme colors
$background-light: #f8fafc;
$surface-light: #ffffff;
$surface-elevated-light: #ffffff;
$text-primary-light: #0f172a;
$text-secondary-light: #475569;
$border-light: #e2e8f0;

// Dark theme colors
$background-dark: #0f172a;
$surface-dark: #1e293b;
$surface-elevated-dark: #334155;
$text-primary-dark: #f8fafc;
$text-secondary-dark: #cbd5e1;
$border-dark: #334155;

$background: $background-light;
$surface: $surface-light;
$surface-elevated: $surface-elevated-light;

$shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
$shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
$shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
$shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
$shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);

// Dark mode shadows
$shadow-sm-dark: 0 1px 2px 0 rgb(0 0 0 / 0.3);
$shadow-dark: 0 1px 3px 0 rgb(0 0 0 / 0.4), 0 1px 2px -1px rgb(0 0 0 / 0.4);
$shadow-md-dark: 0 4px 6px -1px rgb(0 0 0 / 0.4), 0 2px 4px -2px rgb(0 0 0 / 0.4);
$shadow-lg-dark: 0 10px 15px -3px rgb(0 0 0 / 0.4), 0 4px 6px -4px rgb(0 0 0 / 0.4);
$shadow-xl-dark: 0 20px 25px -5px rgb(0 0 0 / 0.4), 0 8px 10px -6px rgb(0 0 0 / 0.4);

$border-radius: 12px;
$border-radius-lg: 16px;
$border-radius-xl: 24px;